@-webkit-keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(41, 41, 107, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(148, 191, 210);
  }

  75% {
    transform: scale(1.2);
    background-color: rgb(46, 102, 126);
  }

  100% {
    transform: scale(1);
    background-color: rgb(11, 122, 170);
  }
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(41, 41, 107, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgb(148, 191, 210);
  }

  75% {
    transform: scale(1.2);
    background-color: rgb(46, 102, 126);
  }

  100% {
    transform: scale(1);
    background-color: rgb(11, 122, 170);
  }
}

@-webkit-keyframes wallAnimation {
  0% {
    transform: scale(0.3);
    background-color: lightgrey;
  }

  50% {
    transform: scale(1.2);
    background-color: grey;
  }

  100% {
    transform: scale(1);
    background-color: rgb(24, 21, 21);
  }
}

@keyframes wallAnimation {
  0% {
    transform: scale(0.3);
    background-color: lightgrey;
  }

  50% {
    transform: scale(1.2);
    background-color: grey;
  }

  100% {
    transform: scale(1);
    background-color: rgb(24, 21, 21);
  }
}

@-webkit-keyframes specialNodes {
  0% {
    transform: scale(.3);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1.0);
  }
}

@keyframes specialNodes {
  0% {
    transform: scale(.3);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1.0);
  }
}
